

@keyframes pulse {
  0% {
    transform: scale3d(1, 1, 1);
    opacity: 1;
  }
  100% {
    transform: scale3d(2.2, 2.2, 1);
    opacity: 0;
  }
}
#pulse:before, #pulse:after {
  animation: 3s ease-out 0s normal none infinite running pulse;
  background: none repeat scroll 0 0 rgba(104, 201, 254, 0.5);
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(104, 201, 254, 0.7) inset;
  content:"";
  height: 40px;
  left: 50%;
  margin: -20px 0 0 -20px;
  pointer-events: none;
  position: absolute;
  top: 50%;
  width: 40px;
  z-index: -1;
}
#pulse:after {
  animation-delay: 1.5s;
}
#pulse {
  width: 50px;
  height: 50px;
  background: #ffffff url('http://www.blogher.com/files/imagecache/tiny_thumb/user_pictures/picture-196261.jpg') no-repeat center center;
  position: relative;
  border-radius: 25px;
  text-align: center;
  font: bold 14px/50px tahoma;
}

#pulse img {
  margin-left: -7px;
  margin-top: 3px;
}