@import "../../Styles/mixin";

.rootHome {
  padding: 30px 0px;

  @include mQ(500px) {
    padding: 30px 10px;
  }

  .titleContainer {
    position: relative;
    width: 85%;
    margin: 0 auto;
    overflow: hidden;
    height: 291px;

    .bg{
      opacity: 0.4;
      width: 100%;
    }

    .markerWrapper{
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }

    .title {
      position: absolute;
      font-size: 2.4vw;
      letter-spacing: 0.7vw;
      font-weight: bold;
      top: 65%;
      left: 50%;
      white-space: nowrap;
      transform: translate(-50%, -50%);
      z-index: 10;

      @include mQ(500px) {
        margin-bottom: 3vw;
        font-size: 5vw;
      }
    }
  }
}